@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  overflow: hidden;
  font-family: 'Lato', sans-serif;
}

.app{
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #499cd2;
  align-items: center;
  height: 100vh;
}

.container{
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

/*Table*/

.app-table table{
  border-collapse: collapse;
  border:0;
  width: 100%;
  margin: 0;
  border-spacing: 0;
}

.app-table tbody {
  display: block;
  height: 70vh;
}

.app-table thead tr{
  display:block;
}

.scroll{
  overflow: auto;
  visibility: hidden;
}

.scroll tbody, .scroll:hover,.scroll:focus {
  visibility: visible;
}

.app-table{
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  width: 100%;
  height: 77vh;
  background-color: aliceblue;
  overflow-y: hidden;
  border-radius: 10px;
  outline: none;
}

.app-table th{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
}

.app-table th:nth-last-child(1){
  padding-right: 40px;
}

.app-table td,th {
  width: 180px;
  border-bottom: #b2b3bc solid 1px;
}

.app-table__rank {
  text-align: center;
}

.app-table__name {
  min-width: 190px;
  text-align: left;
}

.app-table__fields{
  text-align: right;
}

@media screen and (max-width: 1050px){
  .scroll{
    overflow-y: scroll;
    overflow-x: hidden;
    visibility: visible;
  }
  .app-table tr th:nth-child(5), tr td:nth-child(5) {
    display: none;
  }
}

@media screen and (max-width: 740px) {
  .app-table tr th:nth-child(4), tr td:nth-child(4) {
     display: none;
   }
}

.app-table tr {
  -webkit-transition: background-color .3s linear;
  transition: background-color .3s linear;
}

/*.changed_row {*/
/*  background-color: #b2ebff;*/
/*}*/

.decreased_row {
  background-color: rgba(250, 128, 114, 0.4);
}

.increased_row{
  background-color: rgba(144,238,144,0.4);
}

/*Logo*/

.app-logo {
  margin: 15px auto;
  width: 100px;
}

/*Button*/

.app-btn {
  display: flex;
  justify-content: center;
  margin: 10px auto;
}

.app-btn__view {
  border: none;
  font-size: 16px;
  border-radius: 15px;
  padding: 7px 15px;
  color: white;
  background-color: #51c45b;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  text-decoration: none;
  outline: none;
  -webkit-transition: background-color .2s linear;
  transition: background-color .2s linear;
}

.app-btn__view:hover{
  background-color: #1f394b;
}

